* {
  margin: 0; 
  padding: 0; 
color: black;
  -webkit-text-size-adjust: 100%;
  font-family: -apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,"Helvetica Neue",Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol","Noto Color Emoji";
  font-size: 1.1rem;
  font-weight: 400;
}
* body{
  background-color:white;
}
.home{
  background-color: #f5f5f5;
}
.navbar{
  
  padding: 20px;
  display: flex;
  border-bottom: 1px solid whitesmoke;
  background-color: #48abec;
  height: 40vh;
}
.link{
  width: 100%;
}

.navbar h1{
  color :white;
 
}
.navbar links{
  margin-left: auto;
  font-weight: 100;
 
}
.navbar a{
  margin-left: 50px;
  text-decoration: none;
  padding: 6px;
  color: whitesmoke;

}
.details{
  text-align: center;

 
}
.details h1{
  font-size: 60px;
  font-weight: 200;
  margin-top: 15vh;
}
.navbar a:first-child{
  margin-left:250px;
}
.navbar a:hover{
  color:white;
  border-bottom: 2px solid white;
}
.links{
  
 margin-left: 30%;
 display: flex;
}
.content{
  font-size: 30px;
  width: 100%;
}
.blog-list{
  margin-left: 15%;
}
.blog-list h1, .s-content h1, .archives h1, .tags-content h1{
  margin-top: 20px;
  padding: 10px;
  border-left: 5px solid #0078ff;
  padding: 6px;
  font-family: monospace; 
  font-weight:600;
}
.blog-preveiw{
  display: flex;
  align-items: center;
  padding: 10px 16px;
  margin: 20px 0;
  border-bottom: 1px solid whitesmoke;

}
.blog-preveiw:hover{
border-right: 4px solid #0078ff;
}
.blog-preveiw h2{
  font-size: 19px;
  color: black;
  margin-bottom: 8px;
}
.blog-preveiw span{
  font-size: 15px;
  color: rgb(45, 43, 43);
}
.blog-preveiw a{
  text-decoration: none;
}
.blog-details h2{
  font-size: 30px;
  color:#0078ff ;
  margin-bottom: 10px;
  
}
.blog-details div{
  margin: 40px 0;
 
}
.mg-top{
  margin-top: 5%;
  padding: 1rem;
}

button{
  width: 40%;
  background:#0078ff;
  border: 2px solid#0078ff;
  color: #fff;
  padding: 5px;
  font-size: 20px;
  cursor: pointer; 
  margin: 12px 0;
  border-radius: 10px;
}

.login-box{
  width: 280px;
  position: absolute;
  top: 80%;
  left: 50%;
  transform: translate(-50%, -50%);

}
.login-box h1{
  font-size: 30px;
  border-bottom: 4px solid #48abec;
  width: 100%;
  margin-bottom: 5px;
  padding: 5px 0;
  color:#48abec;
}
.textbox{
  width: 100%;
  overflow: hidden;
  font-size: 20px;
  padding: 10px 0;
  margin: 10px 0;
  border-bottom: 1px solid #48abec;
  color: #48abec;
}
.textbox input{
  border: none;
  outline: none;
  background: none;
  font-size: 18px;
  width: 80%;
  float: left;
  margin: 0 10px;
}

textarea{
  border: 2px solid #48abec;
}
select{
  border: 2px solid #48abec;
}
.not-found{
margin-left: 70%;
font-size: 40px;
  
  

}
.blog{
 
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-content: flex-start;
}
.blog-content{

  margin-left: 2%;
  width: 55%;
  display: inline-block;
 
 
}
.content-layout{
  background-color: white;
  padding: 1rem;
  text-align: center;
 }

.content-layout p{
  text-align: left;
  padding: 2px;
  font-size: 15px;
  
  
}
.content-layout h1{
  font-weight: 600;
}

.home-image img{
  width: 100%;
  height: 100%;
}
.home-image{
  background-position: center;
  align-self: center;
  height: 60vh;
  width: 100%;
}
.side-bar{
  width: 30%;
 
}
.search{
background-color: #fff;
width: 100%;
height: 150px;
font-weight: 100;
display: flex;
}

.s-content{
margin-left: 15%;
align-self: center;

}

.search-content{
  width: 80%;
  display: flex;
  align-items: center;
}
.search-input{
  height: 40px;
  padding: 2px;
  top: 0;
  border: 1px solid gray;
}

.search-button{
  top: 0;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
  background-color: #0078ff;
}
.search-icon{
  height: 28px;
  width: 30px;
 
}

.archive, .tags, .recent-post{
  background-color: #fff;;
  padding: 10px;
  margin-top: 10px;
}
.archives, .tags-content h1{
padding: 10px;
margin-left: 15%;
margin-top: 10px;

}
.archives a{
  display: block;
  text-decoration: none;
  font-size: 0.8rem;
  font-weight: 400;
  line-height: 1.5rem;
 

}
.archives a:hover{
  color: #0078ff;
}
.archive-links li::marker{
  color: #0078ff;
 
}
.archive-links li{
  margin-left: 7%;
}
.tag-links{
  margin: auto;
  margin-top: 20px;
  margin-left: 8%;
}
.tags-content a{
  font-size: 15px;
  padding: 0.15rem;
  margin: 3px;
  background-color: #0078ff;
  color: white;
  border-radius: 10px;
  text-decoration: none;

}
.comments{
  margin-top: 2%;
  text-align: left;
  background-color: #fff;;
  padding: 0.2rem;
}
.comments h1, .reply h1{
  font-size: 40px;

}
.comments h1::after, .reply h1::after{
content: "" ;
display:block;
width: 100px;
height:4px;
background-color: #0078ff;
margin: 0.5rem  0;
}
.comment{
  margin-left: 2%;
  
}
.comment p{
text-align: center;
font-size: 15px;
}
h5{
  font-size: 12px;
}
.reply{
  background-color: #fff;
  margin-top: 2%;
  width: 100%;
 
}
.reply input{
  margin: 15px;
  padding: 10px;
  border: 0.5px solid gray;
  border-radius: 5px;
  width: 90%;
  height: 5vh;
}
.reply textarea{
  margin: 15px;
  padding: 10px;
  border: 0.5px solid gray;
  border-radius: 5px;
  width: 90%;
}
.input-flex{
  width: 100%;
  display: flex;
  justify-content: space-around;
}
.reply input:focus , .reply textarea:focus, .search input:focus{
  outline-color: #0078ff; 
}
.reply button{
  margin-left: 15px;
}
.bottom{
  text-align: center;
  background-color: #0078ff;
  height: 25vh;
  padding: 40px;
}
.bottom p{
  font-size: 16px;
  color: white;
}
.create{
  padding: 10rem;
}
